	font-weight: bold;
}

.profileContainer {
	display: flex;
  flex-wrap: wrap;
	justify-content: space-evenly;
  width: 75vw;
}

.profile {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(25vw - 10px);
  margin: 5px;
  margin-bottom: 5vw;
}

.profile div {
  font-size: calc(0.1rem + 1.1vw);
}

.profile a {
  font-size: calc(0.1rem + 1.25vw);
}

.profileImage {
  height: calc(14vw);
  width: calc(14vw);
  margin-bottom: 1vw;
}

.galleryHeader {
  padding-left: 5.5vw;
  padding-bottom: 2vw;
}

.galleryHeader h1 {
  font-size: calc(0.5rem + 2vw);
}

/* Adjustments for smaller screens */
@media screen and (max-width: 767px) {
  .profile {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: calc(100% - 10px);
  }

  .galleryHeader h1 {
    text-align: center;
  }
}
